@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8f9fc;
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 30px 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 30px 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 10px 14px;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  width: 100%;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #102654;
  padding: 10px 14px;
  cursor: pointer;
  background-color: #ffffff;
  transition: all 0.3s ease-in-out;
  user-select: none;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.btn:focus-visible,
.btn:focus,
.btn:hover {
  color: #102654;
  background-color: #ffffff;
}

.btn:active {
  opacity: 0.5;
}

.header {
  min-height: 658px;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1199px) {
  .header {
    min-height: 554px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 491px;
  }
}

.header__top {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  margin-top: 10px;
}
@media (max-width: 1199px) {
  .header__logo {
    width: 82px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 69px;
  }
}

.header__contacts {
  display: flex;
  flex-wrap: wrap;
  max-width: 675px;
  margin-left: auto;
  margin-right: -15px;
  z-index: 1;
}
@media (max-width: 1199px) {
  .header__contacts {
    max-width: 458px;
  }
}
@media (max-width: 767px) {
  .header__contacts {
    max-width: 131px;
  }
}

.header__link {
  position: relative;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  margin-top: 0;
  margin-right: 15px;
  margin-bottom: 10px;
}
@media (max-width: 1199px) {
  .header__link {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .header__link {
    font-size: 20px;
    line-height: 24px;
  }
}
.header__link::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: -3px;
  width: 0;
  height: 2px; /* Высота линии */
  background-color: #102654; /* Цвет подчеркивания при исчезании линии*/
  transition: width 0.5s; /* Время эффекта */
}
.header__link:hover::after, .header__link:focus-visible::after {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 2px; /* Высота линии */
  background-color: #ffffff; /* Цвет подчеркивания при появлении линии*/
  transition: width 0.5s; /* Время эффекта */
}
.header__link:active {
  opacity: 0.5;
}

.header__intro {
  display: grid;
  grid-template-columns: 48% 1fr;
  padding-right: 17.2%;
  margin-top: -20px;
}
@media (max-width: 1199px) {
  .header__intro {
    grid-template-columns: 52% 1fr;
    padding-right: 0;
    padding-top: 28px;
  }
}
@media (max-width: 767px) {
  .header__intro {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 767px) {
  .header__img-wrapper {
    display: none;
  }
}

.header__info {
  display: flex;
}
@media (max-width: 767px) {
  .header__info {
    justify-content: center;
  }
}

.header__title-wrapper {
  display: flex;
  flex-direction: column;
}

.header__img-wrapper img {
  background-color: #0e1356;
}

.header__title-wrapper span {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 112px;
  margin-left: 78px;
}
@media (max-width: 1199px) {
  .header__title-wrapper span {
    justify-content: flex-end;
    font-size: 34px;
    line-height: 41px;
    margin: 50px 3px 0 0;
  }
}
@media (max-width: 767px) {
  .header__title-wrapper span {
    font-weight: 600;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 0.03em;
    margin-left: 0;
    margin-right: 5px;
  }
}

.header__title-wrapper h1 {
  text-transform: uppercase;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  color: #ffffff;
  width: 250px;
  margin: 15px 0 47px 13px;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #ffffff;
}
@media (max-width: 1199px) {
  .header__title-wrapper h1 {
    text-transform: none;
    width: 191px;
    font-size: 72px;
    line-height: 86px;
    margin: 8px 0 36px 20px;
  }
}
@media (max-width: 767px) {
  .header__title-wrapper h1 {
    margin: 16px 0 21px -13px;
  }
}

.header__list {
  margin: 305px 0 30px 20px;
  list-style: none;
  padding: 0;
  width: 113px;
}
@media (max-width: 1199px) {
  .header__list {
    margin: 186px 0 30px;
  }
}
@media (max-width: 767px) {
  .header__list {
    margin: 176px 0 15px;
  }
}

.header__item {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
  color: #ffffff;
}

.header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  min-height: 60px;
  width: 51%;
  margin-left: 163px;
}
@media (max-width: 1199px) {
  .header__button {
    width: 53%;
    margin-left: 139px;
  }
}
@media (max-width: 767px) {
  .header__button {
    min-height: 63px;
    width: 100%;
    margin-left: 0;
  }
}

.gym__container {
  display: grid;
  grid-template-columns: 32% 1fr;
  gap: 88px;
  padding: 120px 103px 120px 163px;
}
@media (max-width: 1199px) {
  .gym__container {
    display: flex;
    gap: 0;
    flex-direction: column;
    align-items: center;
    padding: 66px 44px 120px 44px;
  }
}
@media (max-width: 767px) {
  .gym__container {
    display: flex;
    gap: 0;
    flex-direction: column;
    align-items: center;
    padding: 39px 0 120px;
  }
}

@media (max-width: 767px) {
  .gym__content {
    padding: 0 15px 0;
  }
}

.gym h2 {
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  margin-bottom: 5px;
}
@media (max-width: 1199px) {
  .gym h2 {
    padding-right: 15px;
    margin-bottom: 0;
    display: block;
    text-align: center;
    font-size: 42px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .gym h2 {
    font-size: 32px;
    line-height: 38px;
    text-align: start;
    margin-bottom: 3px;
  }
}

.gym span {
  display: block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  margin-bottom: 40px;
}
@media (max-width: 1199px) {
  .gym span {
    text-align: center;
    margin-bottom: 33px;
  }
}
@media (max-width: 767px) {
  .gym span {
    text-align: start;
    margin-bottom: 23px;
  }
}

.gym p {
  display: block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin-top: 7px;
  margin-bottom: 0;
}
@media (max-width: 1199px) {
  .gym p {
    padding: 0 50px;
    text-align: center;
    margin-bottom: 43px;
  }
  .gym p:not(:first-child) {
    display: none;
  }
}
@media (max-width: 767px) {
  .gym p {
    text-align: start;
    padding: 0 30px 0 0;
    margin-bottom: 35px;
  }
}

@media (max-width: 1199px) {
  .gym__media {
    width: 100%;
  }
}

.gym__media img {
  aspect-ratio: 660/401;
  background-color: #aaacbf;
}
@media (max-width: 1199px) {
  .gym__media img {
    width: 100%;
    height: auto;
    aspect-ratio: 678/345;
  }
}
@media (max-width: 767px) {
  .gym__media img {
    aspect-ratio: 320/313;
  }
}

.gym__video {
  position: relative;
  z-index: 1;
  top: -18%;
  left: -9.6%;
  width: 364px;
}
@media (max-width: 1199px) {
  .gym__video {
    width: 266px;
    height: 167px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 767px) {
  .gym__video {
    top: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: auto;
    margin-top: 40px;
  }
}

.gym__video img {
  width: 364px;
  height: 228px;
  background-color: #4563a3;
}
@media (max-width: 1199px) {
  .gym__video img {
    width: 266px;
    height: 167px;
  }
}
@media (max-width: 767px) {
  .gym__video img {
    width: 100%;
    height: auto;
    aspect-ratio: 320/167;
  }
}

.gym__iframe {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}

.gym__button {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../../img/svg/video-button.svg");
  background-color: transparent;
}
@media (max-width: 1199px) {
  .gym__button {
    width: 73px;
    height: 73px;
  }
}
.gym__button:hover, .gym__button:focus {
  width: 120px;
  height: 120px;
  background-color: transparent;
}
.gym__button:active {
  opacity: 0.5;
  background-color: transparent;
}

.subscription {
  background-color: #f8f9fc;
}

.subscription h2 {
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  display: flex;
  justify-content: center;
}

.subscription__nav {
  display: flex;
  text-align: center;
  position: relative;
  width: fit-content;
  column-gap: 46px;
  margin: 0 auto 38px;
  padding: 0;
}
.subscription__nav::after {
  content: "";
  position: absolute;
  display: block;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(120, 120, 120, 0.2);
}

.subscription__nav-button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  display: inline-block;
  position: relative;
  padding: 12px 0 13px;
  width: fit-content;
  opacity: 0.5;
}
.subscription__nav-button::after {
  content: "";
  position: absolute;
  display: block;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: transparent;
}
.subscription__nav-button:hover {
  opacity: 1;
}
.subscription__nav-button--active {
  opacity: 1;
}
.subscription__nav-button--active::after {
  background-color: #d10a42;
}

[data-sub-nav].no-js {
  display: none;
}

.subscription__subcontent--close {
  display: none;
}
.subscription__subcontent--active {
  display: block;
}

.subscription__content h3 {
  display: flex;
  justify-content: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
}

.subscription__content span {
  display: flex;
  justify-content: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #495a7d;
}

.subscription__list {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.subscription__item {
  padding: 56px 77px 68px;
  border: 7px solid transparent;
  border-image-source: none;
  border-image-slice: 1;
  list-style-type: none;
  background-color: #ffffff;
}
.subscription__item:hover, .subscription__item:focus-within {
  border-image-source: linear-gradient(148.82deg, #d10a42 0.85%, #00359f 100%);
}

.subscription__item:hover .subscription__link,
.subscription__item:focus-within .subscription__link {
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  color: #ffffff;
}

.subscription__item:hover .subscription__link:hover,
.subscription__item:focus-within .subscription__link:hover {
  color: #102654;
}

.subscription__price p {
  display: block;
  text-align: center;
  width: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 150%;
  color: #103aae;
  position: relative;
  z-index: 1;
}
.subscription__price p::before {
  content: attr(data-price);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 100px;
  line-height: 150px;
  font-weight: 700;
  color: #e8e8e8;
  z-index: -1;
}

